import isstr from './isstr.mjs'
import isnum from './isnum.mjs'
import isp0num from './isp0num.mjs'
import isn0num from './isn0num.mjs'
import isint from './isint.mjs'
import ispint from './ispint.mjs'
import isp0int from './isp0int.mjs'
import isnint from './isnint.mjs'
import isn0int from './isn0int.mjs'
import isfun from './isfun.mjs'


/**
 * 
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/verifyValue.test.js Github}
 * @memberOf wsemi
 * @param {String|Function} type 'isstr','isnum','isp0num','isn0num','isint','ispint','isp0int','isnint','isn0int','isfun'
 * @param {*} value 
 * @returns {Object} valueerrerrmsg
 * @example
 * verifyValue('12a5', 'isstr')
 * // => r.err=false
 *
 * verifyValue('12', 'isint')
 * // => r.err=false
 *
 * verifyValue('0', 'isp0int')
 * // => r.err=false
 *
 * verifyValue('12', 'isp0int')
 * // => r.err=false
 *
 * verifyValue('0', 'isn0int')
 * // => r.err=false
 *
 * verifyValue('-12', 'isn0int')
 * // => r.err=false
 *
 * verifyValue('12.34', 'isnum')
 * // => r.err=false
 *
 * verifyValue('12.34', 'isp0num')
 * // => r.err=false
 *
 * verifyValue('-12.34', 'isn0num')
 * // => r.err=false
 *
 */
function verifyValue(value, type) {

    let err = false
    let errmsg = ''
    if (type === 'isstr') {
        if (!isstr(value)) {
            err = true
            errmsg = ''
            value = ''
        }
    }
    else if (type === 'isnum') {
        if (!isnum(value)) {
            err = true
            errmsg = ''
            value = 0
        }
    }
    else if (type === 'isp0num') {
        if (!isp0num(value)) {
            err = true
            errmsg = '0'
            value = 0
        }
    }
    else if (type === 'isn0num') {
        if (!isn0num(value)) {
            err = true
            errmsg = '0'
            value = 0
        }
    }
    else if (type === 'isint') {
        if (!isint(value)) {
            err = true
            errmsg = ''
            value = 0
        }
    }
    else if (type === 'ispint') {
        if (!ispint(value)) {
            err = true
            errmsg = '0'
            value = 0
        }
    }
    else if (type === 'isp0int') {
        if (!isp0int(value)) {
            err = true
            errmsg = '0'
            value = 0
        }
    }
    else if (type === 'isnint') {
        if (!isnint(value)) {
            err = true
            errmsg = '0'
            value = 0
        }
    }
    else if (type === 'isn0int') {
        if (!isn0int(value)) {
            err = true
            errmsg = '0'
            value = 0
        }
    }
    else if (isfun(type)) {
        let f = type
        value = f(value)
    }
    else if (type === 'any') {
        if (!isnum(value) && !isstr(value)) {
            err = true
            errmsg = ''
            value = ''
        }
    }
    else {
        err = true
        errmsg = ''
        value = null
    }
    let r = {
        value: value,
        err: err,
        errmsg: errmsg,
    }
    return r
}


export default verifyValue
