import isint from './isint.mjs'
import cint from './cint.mjs'


/**
 * 
 * 00
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/isnint.test.js Github}
 * @memberOf wsemi
 * @param {*} v 
 * @returns {Boolean} 
 * @example
 * isnint(0)
 * // => false
 *
 * isnint(125)
 * // => false
 *
 * isnint(-125)
 * // => true
 */
function isnint(v) {

    //check
    if (!isint(v)) {
        return false
    }

    let r = cint(v) < 0

    return r
}


export default isnint
